@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  width: 100%;
  height: 100%;
  background: #181818;
  overflow: hidden;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.About{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 100px 250px;
}
.About h1{
    font-size: 5rem;
    font-weight: 900;
    margin-bottom: 33px;
}
.About p{
    text-align: center;
    font-family: 'Archivo', sans-serif;
    line-height: 31px;
}

.About_buttons{
    display: flex;
    margin-top: 33px;
}
.About_buttons button{
    background: #a965cb;
    border-radius: 7px;
    padding: 10px 35px;
    display: flex;
    align-items: center;
    margin-right: 16px;
    border: none;
    outline: none;
}
.About_buttons button:hover{
    opacity: 0.6;
}
.About_buttons button p{
    margin: 11px;
    
    color: rgb(255, 255, 255);
    font-weight: 700;
}
.About_buttons button img{
    height: 30px;
}
/* Remove .img after putting oversea image  */
.img{
    height: 33px;
    width: 33px;
    border: 3px solid cyan;
    border-radius: 50%;
}
.Navbar{
    display: flex;
    justify-content: space-between;
    height: 20vh;
    background: linear-gradient(to right, rgb(245, 162, 245), #271a1c);
    align-items: center;
}
.Navbar_logo{
    margin-left: 55px;
}
.Navbar_logo img{
    height: 60px;
    width: 60px;
    /* width can be removed */
}

.Navbar_items ul{
    z-index: 1;
    display: flex;
}
.Navbar_items ul li{
    list-style: none;
}
.Navbar_items ul li a{
    text-decoration: none;
    color: #fff;
    padding: 0px 11px;
}

.Navbar_connect li{
    list-style: none;
    background: #4C4F56;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    margin-right: 55px;
}
.Navbar_connect li .dot{
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background: red;
}
.Navbar_connect li a{
    text-decoration: none;
    color: #fff;
    font-size: 1.2rem;
    margin-left: 11px;
}
.Navbar_connect:hover{
    opacity: 0.6;
}
.Roadmap{
    display: flex;
    align-items: center;
    padding: 80px 0;
    flex-direction: column;
    justify-content: center;
}
.Roadmap h1{
    font-size: 4rem;
    font-weight: 900;
    margin-bottom: 60px;
}
.Roadmap_detail{
    margin: 20px 0;
    display: flex;
}

.circle{
    height: 18px;
    width: 18px;
    border-radius: 50%;
    background: pink;
    margin-right: 22px;
    margin-top: 7px;
}
.text_heading{
    color: rgb(204, 107, 204);
    font-weight: bold;
    font-size: 1.3rem;
}
.text_detail{
    font-weight: bold;
    font-size: 1.3rem;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.Starter{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 100px;
}
.Starter_text{
    padding: 11px 85px;

}
.Starter_text h1{
    font-family: 'Poppins,Roboto', sans-serif;
    font-size: 5rem;
    margin-bottom: 22px;
}
.Starter_text p{
    font-weight: 600;
    font-family: 'Poppins',sans-serif;
}
.Starter_images {
    position: relative;
    display: flex;
    align-items: flex-end;
    max-width: 55rem;
}
.Starter_images img{
    object-fit: contain;
    height: 95vh;
    padding: 22px 68px;
}

.Starter_navigator{
    display: flex;
    margin-top: 33px;
}
.Starter_navigator button{
    background: rgb(243, 94, 176);
    display: flex;
    align-items: center;
    padding: 17px 40px;
    border: none;
    outline: none;
}
.Starter_navigator button:hover{
    opacity: 0.6;
}
.Starter_navigator button img{
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: cyan;
    margin-right: 18px;
}
.Starter_navigator button h4{
    font-weight: 800;
}

.mint_stat{
    margin-left: 22px;
    display: flex;
    align-items: center;
    padding: 0 11px;
}
.mint_stat p{
    font-size: 0.8rem;
    font-family: sans-serif;
}
.Starter_image{
    height: 30px;
    width: 30px;
    border: 3px solid cyan;
    border-radius: 50%;
    margin-right: 11px;
}

.Teams{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 70px 0;
}
.Teams h1{
    font-size: 4rem;
    font-weight: 900;
    padding-bottom: 70px;
}
.Team_ceo{
    background: linear-gradient(to right, rgb(235, 114, 135), purple);
    width: 205px;
    height: 205px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Team_ceo img{
    height: 200px;
    border-radius: 50%;
    border: 7px solid black;
}

.Ceo_detail{
    margin-top: 25px;
    display: contents;
    flex-direction: column;
    text-align: center;
}
.crown{
    margin-top: 22px;
    height: 29px;
    width: 44px;
}
.Ceo_detail h1{
    font-weight: bold;
    padding-bottom: 0px;
    font-size: 2rem;
}
.Ceo_detail p{
    font-weight: bold;
    color: gray;
}

.Ceo_social{
    display: flex;
    align-items: center;
}
.Ceo_social img{
    height: 28px;
    width: 28px;
    margin-right: 11px;
}


/* EMPLOYEE */
.Team_member{
    display: flex;
    margin-top: 66px;
    margin-right: 55px;
}
.Team_member_list{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-left: 75px;
}
.Team_employee{
    background: linear-gradient(to right, rgb(164, 255, 128), purple);
    width: 205px;
    height: 205px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Team_employee img{
    height: 200px;
    border-radius: 50%;
    border: 7px solid black;
}

.Employee_detail{
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    text-align: center;
}
.Employee_detail h1{
    font-weight: bold;
    padding-bottom: 0px;
    font-size: 2rem;
}
.Employee_detail p{
    font-weight: bold;
    color: gray;
}

.Employee_social{
    display: flex;
    align-items: center;
    justify-content: center;
}
.Employee_social img{
    height: 28px;
    width: 28px;
    margin-right: 11px;
}
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body{
    font-size: 100%;
    color: aliceblue;
    font-family: 'Titillium Web', sans-serif;
}
.Timer_content{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    padding: 7em 0;
}
.Timer_item{
    display: flex;
    margin-left: 22px;
}

.Timer_item section {
    padding: 0 11px;
    display: flex;
}
.Timer_item section p{
    margin-right: 7px;
}

@media screen and (max-width: 820px) {
    .Timer_content{
        display: flex;
        flex-direction: column;
    }
}
